import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { useParams, useNavigate } from 'react-router-dom'
import { UploadDiv, UploadForm, UploadButtonDiv } from "../../Style/UploadCSS";
import '../../Style/Edit.css'
import { useSelector } from 'react-redux';

const Edit = () => {
    let { postNum } = useParams()
    const [postInfo, setPostInfo] = useState({})
    const [title, setTitle] = useState("")
    const [content, setContent] = useState("")
    const [image, setImage] = useState("")
    const navigate = useNavigate()
    const user = useSelector(state=>state.user)
    
    useEffect(()=>{
        if(!user.accessToken){
            alert("    .")
            navigate('/login')
        }
    }, [])

    useEffect(() => {
        let body = {
            postNum: postNum // .
        }
        axios.post("/api/post/detail", body).then((res) => {  // post 
            setPostInfo({
                title: res.data.post.title,
                content: res.data.post.content,
                postNum: res.data.post.postNum,
                image: res.data.post.image
            })
        }).catch(err => {
            console.log(err)
        })
    }, [])

    useEffect(() => {
        setTitle(postInfo.title)         //postinfo      ,   
        setContent(postInfo.content)
        setImage(postInfo.image)
    }, [postInfo])


    const Submit = (e) => {
        e.preventDefault() // 
        if (content === "") {
            return alert(" .")
        }

        let body = {
            title: title,
            content: content,
            postNum: postNum,
            image: image
        }
        if (title === "") {     //  
            body.title = " "
        }

        axios.post('/api/post/edit', body)
            .then(res => {
                alert("  .")
                navigate(-1)
            })
            .catch(err => {
                alert("  .")
                console.log(err)
            })
    }

    return (
        <UploadDiv>
            <UploadForm >
                <label htmlFor="title"><h2></h2></label>
                <input id="title" type="text" value={title} onChange={(e) => { setTitle(e.target.value) }} />
                <br />
                <div className='image'>
                    {postInfo.image ? <><img alt="" src={`http://localhost:5000/${postInfo.image}`}
                        style={{ width: "30%", height: "auto" }}/><div> [ ]</div><br /></> : null}
                </div>
                
                <label htmlFor="content"><h3></h3></label>
                <textarea style={{padding:"15px"}} value={content} onChange={(e) => { setContent(e.target.value) }} />
                <UploadButtonDiv>
                    <button className='cancel' onClick={e => { e.preventDefault(); navigate(-1) }} type="submit"></button>
                    <button type="submit" onClick={(e) => { Submit(e) }}></button>
                </UploadButtonDiv>
            </UploadForm>
        </UploadDiv>
    )
}



export default Edit