import React, { useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom'
import firebase from '../../firebase'
import '../../Style/Form.css'

function Login() {
    const [email, setEmail] = useState("")
    const [password, setPassword] = useState("")
    const navigate = useNavigate()

    const Sign = async (e) => {
        e.preventDefault()
        if (!(email && password)) {
            return alert("  .")
        }
        try {
            await firebase.auth().signInWithEmailAndPassword(email, password)
            alert(" .")
            navigate('/main')
        } catch (err) {
            if (err.code === "auth/user-not-found") {
                alert("  .")
            } else if (err.code === "auth/wrong-password") {
                alert(" .")
            } else if (err.code === "auth/invalid-email") {
               alert("   .")
            }
            else {
                alert(err)
            }
        }
    }

    return (
        <div className='FormContain'>
        
            <form>
                <label></label><br />
                <input type="email" value={email} required onChange={(e) => { setEmail(e.target.value) }} /><br /><br />

                <label></label><br />
                <input type="password" value={password} required onChange={(e) => { setPassword(e.target.value) }} /><br /><br />
                <button onClick={(e) => Sign(e)}></button><br /><br />
                <button onClick={(e) => { e.preventDefault(); navigate('/register') }}></button><br />
                
           </form>
           </div>


    )
}

export default Login