import axios from 'axios'
import React, { useState } from 'react'
import { useSelector } from 'react-redux'

function RepleUpload(props) { //props  id
    const [reple, setReple] = useState("")
    const user = useSelector(state=>state.user)  //user store 

    const RepleUp = (e)=> {
        e.preventDefault()
        if(!reple){
            return alert(" .")
        }
        let body = {
            reple : reple,
            uid: user.uid,
            postId: props.postId
        }

        axios.post('/api/reple/submit', body).then((res)=>{ //    ,   1 
            alert(" .")
            window.location.reload()
        }).catch(err=>{
            alert("  .")
        })
    }

  return (
    <div style={{width:"100%"}}>
        <form style={{marginTop:"50px", marginBottom:"20px", width:"100%"}}>
            <label style={{marginRight: "10px", marginBottom:"5px"}}><h4><b></b></h4></label>
        <textarea style={{marginRight:"20px", width: "100%", height: "100px", padding:"10px"}} value = {reple} onChange={(e)=>setReple(e.target.value)}/>
        <button onClick={(e)=>{RepleUp(e)}}></button>
        </form>
    </div>
  )
}

export default RepleUpload