import { createSlice } from "@reduxjs/toolkit";

export const userSlice = createSlice({ //slice reducer action payload 
    name: "user", //slice 
    initialState: {
        displayName: "",
        uid: "",                         //    
        accessToken: ""
    },
    reducers: { // action  
        loginUser: (state, action) => {            
            state.displayName = action.payload.displayName; //   payload Name 
            state.uid = action.payload.uid;                 //   payload uidfmf 
            state.accessToken = action.payload.accessToken; //   payload  
        }, 
        clearUser: (state) => {       //   action 
            state.displayName = ""; 
            state.uid = "";
            state.accessToken = "";
        }
    }
})

export const {loginUser, clearUser} = userSlice.actions  // slice reducer  export
export default userSlice.reducer  // slice reducer export