import axios from 'axios'
import React, { useState, useEffect } from 'react'
import { useSelector } from "react-redux";
import { useNavigate } from 'react-router-dom'
import firebase from '../../firebase'
import '../../Style/Form.css'

function Register() {
    const [name, setName] = useState("")
    const [email, setEmail] = useState("")
    const [password, setPassword] = useState("")
    const [pwconfirm, setPwconfirm] = useState("")
    const [state, setState] = useState(false)
    const navigate = useNavigate()
    const [nameCheck, setNameCheck] = useState(false)
    const [emailCheck, setEmailCheck] = useState(false)

    const [possible, setPossible] = useState(false)
    const [impossible, setImpossible] = useState(false)
    const [passedName, setPassedName] = useState("")

    const [Epossible, setEpossible] = useState(false)
    const [Eimpossible, setEimpossible] = useState(false)
    const [passedEmail, setPassedEmail] = useState("")

    const user = useSelector((state) => state.user);

    useEffect(() => {
        if (user.accessToken) {
            navigate("/main");           //    
        }
    }, []);

    const NameCheck = (e) => { //  
        e.preventDefault() //     .
        setPassedName(name)       //    

        if (!name) {
            return alert(" "); // name   
        }

        let body = {
            displayName: name       // body .
        }

        axios.post('/api/user/namecheck', body).then(res => {  //body  
            if (res.data.check) { //chek  
                setNameCheck(true) //namechek true 
                setPossible(true)  //    true
                setImpossible(false)  //   false
                setTimeout(() => {
                    setPossible(false)  //3    
                }, 3000);
            } else {                           //check  
                setNameCheck(false) //namechek false 
                setImpossible(true) //    true
                setPossible(false)  //    false
                setTimeout(() => {
                    setImpossible(false) //3     
                }, 3000);
            }
        }).catch(err => {
            console.log("") //  
        })
    }

    const EmailCheck = (e) => { //  
        e.preventDefault() //     .
        setPassedEmail(email)       //    

        let re = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

        if (!re.test(email)) {
            setState(false)
            return alert("   ")
        }

        if (!email) {
            return alert(" "); // name   
        }

        let body = {
            email: email       // body .
        }

        axios.post('/api/user/emailcheck', body).then(res => {  //body  
            if (res.data.check) { //chek  
                setEmailCheck(true) //emailchek true 
                setEpossible(true)  //    true
                setEimpossible(false)  //   false
                setTimeout(() => {
                    setEpossible(false)  //3    
                }, 3000);
            } else {                           //check  
                setEmailCheck(false) //emailchek false 
                setEimpossible(true) //    true
                setEpossible(false)  //    false
                setTimeout(() => {
                    setEimpossible(false) //3     
                }, 3000);
            }
        }).catch(err => {
            console.log("") //  
        })
    }
    
    const Regi = async (e) => {
        e.preventDefault() // 
        setState(true)  //   true . 

        if (!(name && email && password && pwconfirm)) { //    
            setState(false) //   false .
            return alert(' ')
        }

        if (passedName !== name) {   //        
            setState(false) //   false .
            return alert('  .')
        }

        if (!nameCheck) { //   
            setState(false) //   false .
            return alert("   .")
        }

        if (passedEmail !== email) {   //        
            setState(false) //   false .
            return alert('  .')
        }

        if (!emailCheck) { //   
            setState(false) //   false .
            return alert("   .")
        }
        

        if (password !== pwconfirm) { //   
            setState(false) //   false .
            return alert("  .")
        }

        

        let createdUser = await firebase.auth().createUserWithEmailAndPassword(email, password)
        //     
        await createdUser.user.updateProfile({
            displayName: name,
        })
        let body = {
            email: createdUser.user.multiFactor.user.email,
            displayName: createdUser.user.multiFactor.user.displayName,  //    body .
            uid: createdUser.user.multiFactor.user.uid,
        }
        axios.post('/api/user/register', body) //body  
            .then(res => {
                alert(' .')
                navigate('/main')
            })
            .catch(err => {
                console.log(err)
                return alert(" .")
            })


    }

    return (
        <div className='FormContain'>
            <form>
                <label></label><br />
                <input type="name" value={name} onChange={(e) => { setName(e.target.value); }} /><br />
                {possible && <div style={{ color: "blue" }}> .</div>}
                {impossible && <div style={{ color: "red" }}>  .</div>}
                <button style={{ marginTop: "10px" }} onClick={(e) => { NameCheck(e); }}>  </button><br /><br />
                <label></label><br />
                <input type="email" value={email} onChange={(e) => setEmail(e.target.value)} /><br />
                {Epossible && <div style={{ color: "blue" }}> .</div>}
                {Eimpossible && <div style={{ color: "red" }}>  .</div>}
                <button style={{ marginTop: "10px" }} onClick={(e) => { EmailCheck(e); }}>  </button><br /><br />
                <label></label><br />
                <input type="password" value={password} minLength={8} onChange={(e) => setPassword(e.target.value)} /><br /><br />
                <label> </label><br />
                <input type="password" value={pwconfirm} minLength={8} onChange={(e) => setPwconfirm(e.target.value)} /><br /><br />
                <button disabled={state} onClick={(e) => { Regi(e) }}></button>
            </form>
        </div>
    )
}

export default Register