import React  from "react";
import { ListItem, ListDiv } from "../../Style/ListCSS";
import { Link } from "react-router-dom";
import moment from 'moment'
import 'moment/locale/ko'

const List = (props) => {  // props  props.postList      .

    const UploadTime = (createTime, updateTime)=>{
        if(createTime!==updateTime){
            return moment(updateTime).format('YYYY MMMM Do, a hh mm ss')+'()'
        }else{
            return moment(createTime).format('YYYY MMMM Do, a hh mm ss')
        }
    }
    return (
        <ListDiv>
        <h3></h3>
        <hr/>
            {
                props.postList.map((post, idx) => {      //  mapping. post   , idx  
                    return (
                        <ListItem key={idx}>
                            <Link to={`/post/${post.postNum}`}> {/*     */}
                            <p className="title"> <h4> <b>{post.title} </b></h4></p>
                            <p> : {post.author.displayName}</p>
                            <p>{post.content}</p>
                            <p>{UploadTime(post.createdAt, post.updatedAt)}</p>
                            <hr />
                            </Link>
                        </ListItem>
                    )
                })
            }
        </ListDiv>
    )
}

export default List