import axios from 'axios'
import React from 'react'
import { useParams, Link, useNavigate } from 'react-router-dom'
import { useSelector } from 'react-redux'
import "../../Style/Detail.css"

function Detail(props) {  //PostArea props . props.postinfo     .
  const navigate = useNavigate()

  const user = useSelector(state => state.user)

  const { postNum } = useParams() //   

  const Delete = () => {
    if (!(window.confirm("?"))) {
      return
    } else {
      let body = {
        postNum: postNum          //   
      }
      axios.post("/api/post/delete", body)
        .then(res => {
          alert(" .")
          navigate(-1)

        }).catch(err => {
          alert("  .")
        })
    }
  }

  return (
    <div className='Box'>
      <h2><b>{props.postInfo.title}</b></h2><br/>
      <h5> : {props.postInfo.author.displayName}</h5>
      <br/>
        <p>{props.postInfo.image ? <img src={`http://localhost:5000/${props.postInfo.image}`}// static  
          style={{ width: "40%", height: "auto" }} /> : null}</p>
      <p><div><h4>{props.postInfo.content}</h4></div></p>
      {user.uid === props.postInfo.author.uid &&  //  user uid   
        (<div className='TwoButtons'>
          <button id="modifyButton"><Link to={`/edit/${postNum}`}></Link></button>
          <button onClick={() => Delete()}></button><br />
        </div>)}
    </div>
  )
}

export default Detail