import React, { useState, useEffect } from 'react'
import axios from 'axios'
import List from './Post/List'
import '../Style/MainPage.css'
import { useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";

function MainPage() {
    const [sort, setSort] = useState("")
    const [sortState, setSortState] = useState(true)
    const [postList, setPostList] = useState([])
    const [searchTerm, setSearchTerm] = useState("")
    const [skip, setSkip] = useState(0)
    const [loadMore, setLoadMore] = useState(true)

    const navigate = useNavigate()
    const user = useSelector(state => state.user)  //user store 

    useEffect(() => {   //  .
        if (!user.accessToken) {
            alert("    .")
            navigate('/login')
        }
    }, [])

    const getLoadMore = () => { //    .
        let body = {
            sort: sort,
            searchTerm: searchTerm,
            skip: skip
        }
        axios.post('/api/post/list', body)
            .then(res => {
                setPostList([...postList, ...res.data.postList])
                setSkip(skip + res.data.postList.length)
                if (res.data.postList.length < 3) {
                    setLoadMore(false)
                }
            }).catch(err => {
                console.log(err)
            })
    }
    const getPostList = () => {
        setSkip(0) //skip 0 
        let body = {      // , skip  .
            sort: sort,
            searchTerm: searchTerm,
            skip: 0
        }
        axios.post('/api/post/list', body) // 
            .then(res => {
                setPostList([...res.data.postList])
                setSkip(res.data.postList.length)
                if (res.data.postList.length < 3) {
                    setLoadMore(false)
                }else{
                    setLoadMore(true)
                }
            }).catch(err => {
                console.log(err)
            })
    }
    useEffect(() => {  //sort    
        getPostList()
    }, [sort])

    return (
        <div>
            <div className='SearchAndArrange'>
                <button disabled={sortState} onClick={() => { setSort(""); setSortState(true) }}></button>
                <button disabled={!sortState} style={{ marginLeft: "5px", marginRight: "20px" }} onClick={() => { setSort(""); setSortState(false) }}></button>
                <input style={{ marginRight: "5px" }} placeholder='  ' type="text" value={searchTerm} onChange={e => setSearchTerm(e.target.value)} />
                <button onClick={() => getPostList()}></button>
                <button style={{ marginLeft: "10px" }} onClick={() => navigate('/upload')}> </button>
            </div>
            <List postList={postList} />
            {loadMore && (<button className='SeeMore' onClick={() => getLoadMore()}> </button>)}
        </div>
    )
}

export default MainPage