import React, { useEffect, useRef, useState } from 'react'
import { useSelector } from 'react-redux'
import axios from 'axios'
import '../../Style/Replecontent.css'
import moment from 'moment'

function Replecontent(props) {//props   id    
    const [state, setState] = useState(false)
    const ref = useRef()
    const user = useSelector(state => state.user) //user store 
    const [editFlag, setEditFlag] = useState(false)
    const [reple, setReple] = useState(props.reple.reple)

    useOnclickOutside(ref, () => setState(false)) //  

    const RepleTime = (createTime, updateTime)=>{
        if(createTime!==updateTime){
            return moment(updateTime).format('YYYY MMMM Do, a hh:mm:ss')+'()'
        }else{
            return moment(createTime).format('YYYY MMMM Do, a hh:mm:ss')
        }
    }

    const RepleDel = (e) => {
        e.preventDefault()
        if(window.confirm("?")){
            let body = {
                repleId : props.reple._id,
                postId : props.reple.postId
            }
            axios.post("/api/reple/delete", body)
                .then(res => {
                    alert(" .")
                    window.location.reload()
    
                }).catch(err => {
                    alert("  .")
                })
        }
        return
    }

    const RepleMod = (e) => {
        e.preventDefault()
        if (!reple) {
            return alert(" .")
        }
        let body = {
            reple: reple,
            uid: user.uid,
            postId: props.reple.postId,
            repleId: props.reple._id
        }

        axios.post('/api/reple/edit', body).then((res) => {
            alert(" .")
            window.location.reload()
        }).catch(err => {
            alert("  .")
        })
    }

    return (
        <div>

            <hr /><p><b>{props.reple.author.displayName}</b></p>

            {editFlag ? (   // editFlag  ,  . 
                <div>
                    <form>
                        <input type="text" value={reple} onChange={(e) => setReple(e.target.value)} />
                        <button onClick={(e) => { RepleMod(e) }}></button>
                    </form>
                    <button onClick={(e) => { e.preventDefault(); setEditFlag(false) }}></button>
                </div>)
                : <div>
                    <p>{props.reple.reple}</p> 
                    <div>{RepleTime(props.reple.createdAt, props.reple.updatedAt)}</div>
                    </div>}

            {    //    uid   uid  
                props.reple.author.uid === user.uid && <div className='OnCursor'>  
                    <span onClick={() => setState(!state)}><b>...</b></span>
                    {state &&
                        <div ref={ref}>
                            <p onClick={() => { setEditFlag(true); setState(false) }}></p> {/*     */}
                            <p onClick={(e) => RepleDel(e)}></p>
                        </div>}
                </div>
            }
        </div>
    )
}

function useOnclickOutside(ref, handler) {
    useEffect(() => {
        const listener = (e) => {
            if (!ref.current || ref.current.contains(e.target)) {
                return
            }
            handler(e)
        }
        document.addEventListener("mousedown", listener)
        document.addEventListener("mousedown", listener)
        return () => {
            document.removeEventListener("mousedown", listener)
            document.removeEventListener("touchstart", listener)
        }
    },
        [ref, handler])
}

export default Replecontent