import React from "react";
import { Link, useNavigate } from "react-router-dom";
import { Navbar, Container, Nav, } from 'react-bootstrap'
import { useSelector } from "react-redux";
import firebase from "../firebase";

const Header = () => {
    const navigate= useNavigate()
    const user = useSelector(state => state.user) //store state   .
    const Logout = ()=>{
         firebase.auth().signOut() //   
         alert(" .")
         navigate('/') //  
    }

    return (
        <>
            <Navbar bg="dark" expand="lg" variant="dark">
                <Container>
                    <Navbar.Brand><Link to='/main' style={{color: "white", textDecoration:"none", marginRight: "10px"}}></Link></Navbar.Brand>
                    <Navbar.Toggle aria-controls="basic-navbar-nav" />
                    <Navbar.Collapse id="basic-navbar-nav">
                        <Nav className="me-auto">        {/*Link     . */}
                        <Nav.Link><Link to="/main" style={{color: "white", textDecoration:"none", marginRight: "10px"}}>Home</Link></Nav.Link>
                           <Nav.Link><Link to="/upload" style={{color: "white", textDecoration:"none", marginRight: "10px"}}>Upload</Link></Nav.Link>  
                        </Nav>
                    </Navbar.Collapse>
                    <Navbar.Collapse className="justify-content-end">
                        {user.accessToken ===""
                        ? <Nav.Link><Link to="/login" style={{color: "white", textDecoration:"none"}}></Link></Nav.Link> 
                        : <Navbar.Text style={{color : "white", cursor: "pointer"}} onClick={()=>Logout()}>Logout</Navbar.Text>}
                    </Navbar.Collapse>
                </Container>
            </Navbar>

        </>
    )
}

export default Header