import axios from "axios";
import React, { useEffect, useState, } from "react";
import { useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { UploadDiv, UploadForm, UploadButtonDiv } from "../../Style/UploadCSS";
import ImageUpload from "./ImageUpload";

const Upload = (props) => {
    const navigate = useNavigate()
    const [title, setTitle] = useState("")
    const [content, setContent] = useState("")
    const [image, setImage] = useState("")

    const user = useSelector(state => state.user)       //store user 
    useEffect(() => {
        if (!user.accessToken) {         //user   
            alert("     .")
            navigate('/login')
        }
    }, [])

    const Submit = (e) => {
        e.preventDefault()  // 
        if (content === "") {       //   
            return alert(" .")
        }

        let body = {
            title: title,
            content: content,
            image: image,
            uid: user.uid
        }

        if (title === "") {           //  
            body.title = " "
        }

        axios.post('api/post/submit', body)           // body  
            .then(res => {
                alert("  .")
                setTitle("")
                setContent("")
                navigate('/main')
            })
            .catch(err => {
                alert("  .")
                console.log(err)
            })
    }

    return (
        <UploadDiv>
            <UploadForm onSubmit={(e) => { Submit(e) }}>
                <label htmlFor="title"><h2></h2></label>
                <input id="title" type="text" value={title} onChange={(e) => { setTitle(e.target.value) }} />
                <br />
                <ImageUpload setImage={setImage} />
                <br />
                <label htmlFor="content"><h3></h3></label>
                <textarea style={{ padding: "15px" }} value={content} onChange={(e) => { setContent(e.target.value) }} />
                <UploadButtonDiv>
                    <button className='cancel' onClick={e => { e.preventDefault(); navigate(-1) }} type="submit"></button>
                    <button type="submit"></button></UploadButtonDiv>
            </UploadForm>
        </UploadDiv>
    )
}

export default Upload